import { Component, OnInit } from '@angular/core';
import { NavigationEnd, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import jsPDF from 'jspdf';
import html2canvas from 'html2canvas';
@Component({
    selector: 'app-header',
    templateUrl: './header.component.html',
    styleUrls: ['./header.component.scss']
})
export class HeaderComponent implements OnInit {
    public pushRightClass: string;

    constructor(private translate: TranslateService, public router: Router) {
        this.router.events.subscribe((val) => {
            if (val instanceof NavigationEnd && window.innerWidth <= 992 && this.isToggled()) {
                this.toggleSidebar();
            }
        });
    }

    ngOnInit() {
        console.log("set",jsPDF,html2canvas);
        this.pushRightClass = 'push-right';
    }
    generatePdf() {
        const div = document.getElementById("html2Pdf");
        const options = {background: "white", height: div.clientHeight, width: div.clientWidth};

        html2canvas(div, options).then((canvas) => {
            //Initialize JSPDF
            let doc = new jsPDF("p", "mm", "a4");
            let width = doc.internal.pageSize.getWidth();
            let height = doc.internal.pageSize.getHeight();
            //Converting canvas to Image
            let imgData = canvas.toDataURL("image/PNG");
            //Add image Canvas to PDF
            doc.addImage(imgData, 'PNG', 2, 2,width,height);

            let pdfOutput = doc.output();
            // using ArrayBuffer will allow you to put image inside PDF
            let buffer = new ArrayBuffer(pdfOutput.length);
            let array = new Uint8Array(buffer);
            for (let i = 0; i < pdfOutput.length; i++) {
                array[i] = pdfOutput.charCodeAt(i);
            }

            //Name of pdf
            const fileName = "example.pdf";

            // Make file
            doc.save(fileName);

        });
    }



    generatePdf2() {
        const div = document.getElementById("html2Pdf2");
        const options = {background: "white", height: div.clientHeight, width: div.clientWidth};

        html2canvas(div, options).then((canvas) => {
            //Initialize JSPDF
            let doc = new jsPDF("p", "mm", "a4");
            let width = doc.internal.pageSize.getWidth();
            let height = doc.internal.pageSize.getHeight();
            //Converting canvas to Image
            let imgData = canvas.toDataURL("image/PNG");
            //Add image Canvas to PDF
            doc.addImage(imgData, 'PNG', 2, 2,width,height);

            let pdfOutput = doc.output();
            // using ArrayBuffer will allow you to put image inside PDF
            let buffer = new ArrayBuffer(pdfOutput.length);
            let array = new Uint8Array(buffer);
            for (let i = 0; i < pdfOutput.length; i++) {
                array[i] = pdfOutput.charCodeAt(i);
            }

            //Name of pdf
            const fileName = "example2.pdf";

            // Make file
            doc.save(fileName);

        });
    }
    // htmltoPDF()
    // {
    //     // parentdiv is the html element which has to be converted to PDF
    //     html2canvas(document.querySelector("#parentdiv")).then(canvas => {

    //       var pdf = new jspdf('p', 'pt', [canvas.width, canvas.height]);

    //       var imgData  = canvas.toDataURL("image/jpeg", 1.0);
    //       pdf.addImage(imgData,0,0,canvas.width, canvas.height);
    //       pdf.save('converteddoc.pdf');

    //   });
    // }
    isToggled(): boolean {
        const dom: Element = document.querySelector('body');
        return dom.classList.contains(this.pushRightClass);
    }

    toggleSidebar() {
        const dom: any = document.querySelector('body');
        dom.classList.toggle(this.pushRightClass);
    }

    rltAndLtr() {
        const dom: any = document.querySelector('body');
        dom.classList.toggle('rtl');
    }

    onLoggedout() {
        localStorage.removeItem('isLoggedin');
    }

    changeLang(language: string) {
        this.translate.use(language);
    }
}
